@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

.container {
  background-color: #010114;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.inner_container {
  border: 8px solid #010114;
  width: 80vw;
  height: 85vh;
}

.header_box {
  color: #fff;
  background-color: #272775b4;
  border-bottom: 8px solid #010114;
  justify-content: center;
  align-items: center;
  height: 15%;
  display: flex;
}

.header_box h1 {
  font-weight: 200;
}

.content_box {
  height: 85%;
  display: flex;
}

.crypto-selector {
  background-color: #1e1e1e;
  border-right: 8px solid #010114;
  width: 20%;
  height: 100%;
}

.crypto-selector-header {
  border-bottom: 1px solid #fff;
  height: 10%;
}

.chart_content_container {
  background-color: #1e1e1e;
  width: 100%;
}

.interval-selector {
  border-bottom: 1px solid #fff;
  height: 10%;
}

#crypto-toggle button {
  width: 100%;
  height: 7vh;
}

#interval-toggle {
  height: 100%;
  display: flex;
}

#interval-toggle button {
  height: 100%;
}

#chart-container {
  width: 100%;
  height: 90%;
}

.toggle-btn img {
  height: 70%;
}

.toggle-btn {
  cursor: pointer;
  color: #fff;
  background-color: #5757aab4;
  border: 2px solid #333;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  font-weight: 500;
  transition: background-color .3s, color .3s;
  display: flex;
}

.toggle-btn.active {
  color: #fff;
  background-color: #1f1f74b4;
}

.toggle-btn:hover {
  background-color: #4646b6b4;
}

